import './App.css';
import {productData} from './components/ProductData';
import Products from './components/ProductInfo';
import React, { useState,} from 'react';
import Cart from './components/Cart';


function App() {
  const [products, setProduct] = useState(productData);
  const[show,setShow] = useState(false);
  const[showCheckout, setShowCheckout] = useState(false);
  const[showLast, setShowLast] = useState(false);
  const[checkout, setCheckout] = useState({name: "", address: "", email: "", phone: "", postcode: "", district: ""})

  const checkoutHandler = (c, info) => {
      const _checkout = { ...checkout };
      _checkout[info] = c.target.value;
      setCheckout(_checkout);
}
const buttonSubmit = (e) =>{
  e.preventDefault();
  checkout.name && checkout.address && checkout.district && checkout.email && checkout.phone && checkout.postcode && setShowLast(!showLast)
}
  const filteredProducts = products.filter(p => p.counter > 0)

  const zeroProducts = () => {
    const tempProducts = products.map(p => {
        p = { ...p, counter: 0 };
        return p;
    });
    setProduct(tempProducts);
}

  return (
  <div className="App">
      <header className="App-header">
        <p>Fanikauppa</p>
        <img src="https://i.imgur.com/j3owMWw.png" alt="ostoskärry" onClick={()=>setShow(!show)}/>
      </header>
    <div className="Products" style={{width: 350}}>
      <Products style={{paddingRight: 100}} products={products} setProducts={setProduct}/>
    </div>
    {show &&
    filteredProducts.length > 0 ?
    <div id="ostoskori">
      <div id="cartwrapper">
        <Cart id="cart" products={products} setProducts={setProduct} />
      </div>
      {filteredProducts.length > 0 && <button onClick={()=>setShowCheckout(!showCheckout)}>Checkout</button>}
    </div> : 
        show && <div id="ostoskori">
        <p>Ei tuotteita ostoskorissa</p>
      </div>
    }

 
    {showCheckout &&
    <>
    <form id="form" style={{borderRadius: 10}}>
      <label>
        Name:
        <input type="text" name="name" value={checkout.name} onChange={c => checkoutHandler(c, 'name')} required />
        <br></br>
      </label>
      <label>
        Email:
        <input type="text" email="email" required value={checkout.email} onChange={c => checkoutHandler(c, 'email')}/>
        <br></br>
      </label>
      <label>
        Phone:
        <input type="text" phone="phone" required value={checkout.phone} onChange={c => checkoutHandler(c, 'phone')}/>
        <br></br>
      </label>
      <label>
        Address:
        <input type="text" address="address"  value={checkout.address} onChange={c => checkoutHandler(c, 'address')} required/>
        <br></br>
      </label>
      <label>
        Postcode:
        <input type="text" postcode="postcode" required value={checkout.postcode} onChange={c => checkoutHandler(c, 'postcode')}/>
        <br></br>
      </label>
      <label>
        Postal district:
        <input type="text" district="district" required value={checkout.district} onChange={c => checkoutHandler(c, 'district')}/>
        <br></br>
      </label>
    </form>
    <button style={{position:'relative', marginRight: '5%'}} onClick={buttonSubmit}>Confirm order</button>
    </>}


    {showLast &&
    <div id="modal">
    </div>}
    {showLast &&
      <div id="insidemodal" style={{borderRadius: 10}}>
          <p>Kiitos tilauksesta {checkout.name}!</p>
          <p>Tilauksesi: <Cart id="cart" products={products}/> Toimitetaan osoitteeseen {checkout.address} seuraavan 5-10 päivän sisällä</p>
          <button style={{marginBottom: 10}} onClick={()=>[setShowLast(!showLast), setShow(!show), setShowCheckout(!showCheckout), setCheckout(""), zeroProducts()]}>Sulje tilaus</button>
      </div>}

    
  </div>
  );
}

export default App;
